
"use client";

import { Sidebar, Modal } from "flowbite-react";
import {HiChartSquareBar, HiCash, HiClipboard, HiUser, HiOutlinePlus } from "react-icons/hi";

import { use, useState } from "react";
import { useRouter } from "next/navigation";

import ModalMov from "./ModalMov";

import Image from "next/image";

export default function DashboardSidebar() {

  const [openModalMov, setOpenModalMov] = useState(false);

  const useNav = ()=>{
    const router = useRouter();

    const navigate = (path) => {
      router.push(path);
    }

    return {navigate};
  }

  const {navigate} = useNav();

  return (
    <Sidebar aria-label="Sidebar with content separator example" className="h-screen">
        <Modal show={openModalMov} onClose={() => setOpenModalMov(false)}>
          <ModalMov/> 
        </Modal>
        <div className="flex items-center h-16">
          <Image src="/logo-black.png" alt="Logo" width={80} height={80} />
          <h1 className="text-2x1">A.G.I.O.T.A</h1>
        </div>
      <Sidebar.Items>
      <Sidebar.ItemGroup>

      </Sidebar.ItemGroup>
        <Sidebar.ItemGroup>
          <div onClick={() => navigate("/dashboard")}>
          <Sidebar.Item href="dashboard" icon={HiChartSquareBar}>
            Painel
          </Sidebar.Item>
          </div>
          <Sidebar.Item href="dashboard/extrato" icon={HiCash}>
            Extrato
          </Sidebar.Item>
          <Sidebar.Item href="dashboard/contatos" icon={HiClipboard}>
            Contatos
          </Sidebar.Item>
          <Sidebar.Item href="#" icon={HiOutlinePlus} onClick={() => setOpenModalMov(true)}>
            Adicionar movimentação
          </Sidebar.Item>
        </Sidebar.ItemGroup>
      </Sidebar.Items>
    </Sidebar>
    
  );
}
